/*
<< 600px: Phone
600px - 900px: Portrait Tablet
900px - 1200px: Landscape Tablet
1200px - 1800px: Desktop
>> 1800px : Big desktop

$breakpoint argument choices:
- phone
- tab-port
- tab-land
- big-desktop

ORDER: Base + typography > general layout + grid> page layout > components
*/
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%;
  /*1rem = 10px*/ }
  @media only screen and (min-width: 112.5em) {
    html {
      font-size: 75%; } }
  @media only screen and (max-width: 75em) {
    html {
      font-size: 56.25%; } }
  @media only screen and (max-width: 56.25em) {
    html {
      font-size: 50%; } }

body {
  box-sizing: border-box;
  padding: 3rem;
  /*borde blanco*/ }
  @media only screen and (max-width: 56.25em) {
    body {
      padding: 0; } }

::selection {
  background-color: #84d619;
  color: #fff; }

@keyframes moveInLeft {
  0% {
    /*inicio de la animacion*/
    opacity: 0;
    transform: translateX(-100px);
    /*100 pixeles a la izquierda*/ }
  /*
    80% {
        transform: translateX(10px) //para un efecto de rebote, se pasa de la posicion inicial
    }*/
  100% {
    /*fin de la animacion*/
    opacity: 1;
    transform: translate(0);
    /*vuelve a la posicion inicial*/ } }

@keyframes moveInRight {
  0% {
    opacity: 0;
    transform: translateX(100px); }
  100% {
    opacity: 1;
    transform: translate(0); } }

@keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(-3rem); }
  100% {
    opacity: 1;
    transform: translate(0); } }

.heading-primary {
  color: #fff;
  text-transform: uppercase;
  backface-visibility: hidden;
  /*evitar shake al final de ciertas animaciones*/
  margin-bottom: 6rem; }
  .heading-primary--main {
    display: block;
    /*hace que ocupe el bloque completo creando un linebreak (por defecto es inline)*/
    font-size: 6rem;
    font-weight: 400;
    letter-spacing: 3.5rem;
    animation-name: moveInLeft;
    animation-duration: 2s;
    /*
        animation-timing-function: ease-out;
        animation-iteration-count: 3;
        animation-delay: 3s
        */ }
    @media only screen and (max-width: 37.5em) {
      .heading-primary--main {
        letter-spacing: 2rem;
        font-size: 4rem; } }
    @media only screen and (max-width: 20em) {
      .heading-primary--main {
        letter-spacing: 1.5rem;
        font-size: 3.5rem; } }
  .heading-primary--sub {
    display: block;
    font-size: 2rem;
    font-weight: 700;
    letter-spacing: 1.75rem;
    animation-name: moveInRight;
    animation-duration: 2s; }
    @media only screen and (max-width: 37.5em) {
      .heading-primary--sub {
        letter-spacing: .8rem;
        font-size: 1.5rem; } }
    @media only screen and (max-width: 20em) {
      .heading-primary--sub {
        letter-spacing: .6rem;
        font-size: 1.3rem; } }

