.btn, .btn:link, .btn:visited {
  /*estado especial*/
  font-size: 1.6rem;
  text-transform: uppercase;
  text-decoration: none;
  padding: 1.5rem 4rem;
  display: inline-block;
  border-radius: 10rem;
  border: none;
  transition: all .2s;
  /* animar todas las propiedades que cambian*/
  position: relative; }

.btn:hover {
  transform: translateY(-0.3rem);
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2); }
  .btn:hover::after {
    transform: scaleX(1.4) scaleY(1.6);
    opacity: 0; }

.btn:active, .btn:focus {
  /*click = active*/
  outline: none;
  transform: translateY(-0.1rem);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2); }

.btn--white {
  background-color: #fff;
  color: #777; }
  .btn--white::after {
    background-color: white;
    color: #777; }

.btn--green {
  background-color: #84d619;
  color: #fff; }
  .btn--green::after {
    background-color: #84d619; }

.btn::after {
  /*child del boton, necesitamos que tenga las mismas dimensiones*/
  content: "";
  display: inline-block;
  height: 100%;
  width: 100%;
  border-radius: 10rem;
  position: absolute;
  top: 0;
  /*necesita un padre que tenga positon relative, en este caso, el mismo boton*/
  left: 0;
  z-index: -1;
  transition: all .4s; }

.btn--animated {
  animation: moveInBottom 1s ease-out .75s;
  animation-fill-mode: backwards;
  /*aplica los estilos del principio de la animacion para que no se muestre el boton antes de la animacion*/ }

.btn-text:link, .btn-text:visited {
  font-size: 1.6rem;
  color: #84d619;
  display: inline-block;
  text-decoration: none;
  border-bottom: 1px solid #84d619;
  padding: 1rem 3rem;
  border-radius: 100px;
  transition: all .2s; }

.btn-text:hover {
  background-color: #84d619;
  color: #fff;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.15);
  transform: translateY(-2px); }

.btn-text:active {
  transform: translateY(0);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15); }

